@import url(//db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
@import url(https://fonts.googleapis.com/css2?family=Finger+Paint&family=Macondo&display=swap);

body {
  margin: 0;
  padding: 0;
}
.game {
  margin: auto;
  -webkit-text-stroke: 2px #2a324b;
  font-family: 'Pokemon' ,'Macondo', cursive;
  color: #ef3e2e;
  letter-spacing: 4px;
  text-align: center;
  background-image: url("https://wallpapercave.com/wp/cqhO8rQ.jpg") ;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 2;
  min-height: 100vh;
}
.heder {
  -webkit-text-stroke: 4px #2a324b;
  font-size: 8rem;
  position: absolute;
  left: 10%;
  right: 10%;
  z-index: 1;
  top:20%;
  max-width: 100vw;
  max-height: 100vh;
  display: block;

  animation: textZoom 3.5s ease-in-out;
}

@keyframes textZoom {
  0% {
  transform: scale(0);
  }
  70% {
  transform: scale(1.5);
  }
  100% {
    transform: scale(0);
    }
  }
.card-grid {
  display: flex;
  flex-wrap: wrap;
}

.game-data {
  display: flex;
  justify-content: space-evenly;
  font-size: 4vh;
  background-color: #c7ccdb;
}

.card {
  position: relative;
  margin:1rem 1rem 1rem  auto;
  justify-content: space-between;
  
}
.card img {
  width: 100%;
  max-width: -webkit-fit-content ;
  max-width: -moz-fit-content ;
  max-width: fit-content ;
  height: auto 0;
  display: block;
  border: 3px dotted #240606;
  border-radius: 10px;
}

.card .front {
  transform: rotateY(90deg);
  transform: all ease-in-out 0.3s;
  position: absolute;
  margin: 0 auto;
  border: transparent;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.3s;
}

.card .back {
  transition: all ease-in-out 0.3s;
  transition-delay: 0.3s;

}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;

}

